import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { HomepageComponent } from './homepage/homepage.component';
import { FrontendComponent } from './frontend/frontend/frontend.component';
import { DatabaseComponent } from './database/database/database.component';
import { EssaysComponent } from './essays/essays/essays.component';

import { backendRouter } from './router/backend'

const routes: Routes = [...backendRouter,
  {path: 'homepage',component: HomepageComponent},
  {
    path: 'frontend',
    component: FrontendComponent,
    children: []
  },
  {
    path: 'database',
    component: DatabaseComponent,
    children: []
  },
  {
    path: 'essays',
    component: EssaysComponent,
    children: []
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
